<script>
import LogPageMain from "../components/log/main";
import NewInfo from "../components/log/newInfo";
import SectionTitle from "../components/section-title";
import ForumList from "../components/forum/forumList"
import ForumPageMain from "../components/forum/main";
import Discussion from "./Discussion";
import {fetchSuggest} from "../api";
import post from '../components/post'

export default {
  name: "UserContent",
  components: {ForumPageMain, ForumList, SectionTitle, NewInfo, LogPageMain, post},
  data() {
    return {
      sugPeer: [],
      sugQuanzi: [],
      articles: [],
    }
  },
  methods: {
    getSugList() {
      fetchSuggest().then(res=>{
        this.sugPeer = res.peer || []
        this.sugQuanzi = res.quanzi || []
      })
    },
    getArticles() {
      this.$axios(
          {
            url: '/post/list',
            method: 'get'
          }
      ).then(res => {
        // console.log(res)
        this.articles = res.data.data.items || []
        // console.log(this.articles)
        // console.log('get articles ok')
      }).catch(err => {
        console.log(err)
      })
    },
  },
  mounted() {
    this.getSugList();
    this.getArticles()
  }
}
</script>

